@import "../../variable.scss";

.home-page {
  display: flex;
  padding: 50px;
  min-height: 700px;
  flex-direction: column;
  justify-content: center;
  background: #171717;

  #title {
    font-size: 60px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #F18F43;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  }
  #sub-title {
    margin: 0;
    font-size: 16px;
    color: #e3c4c4;
  }
  #download-cv {
    width: 200px;
    color: #fff;
    border: none;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    margin-top: 15px;
    text-align: center;
    padding: 10px 20px;
    text-decoration: none;
    text-transform: uppercase;
    background: $primary-color;
  }
  @media screen and (max-width: 600px) {
    min-height: 500px;
    padding: 50px 30px;
    background-position: 60%;
    #title {
      font-size: 40px;
    }
    #sub-title {
      font-size: 18px;
    }
  }
}
